import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    form: false,
  },

  minRatio: {
    displayName: 'minRatio',
    formConfig: {
      overwriteRule: true,
      rule: [
        v => /^(-)?[0-9]+\.{0,1}[0-9]{0,2}$/.test(v) || 'please enter the correct min Ratio',
      ],
    },
  },

  maxRatio: {
    displayName: 'maxRatio',
    formConfig: {
      overwriteRule: true,
      rule: [
        v => /^(-)?[0-9]+\.{0,1}[0-9]{0,2}$/.test(v) || 'please enter the correct max Ratio',
      ],
    },
  },
  color: {
    type: IKDataEntity.Types.Color,
    formConfig: {
      required: true,
    },
  },

}

const config = {

  add: function (item) {
    return hillo.postWithUploadFile('DCache.php?op=createDCacheColor', item, {
      showLoading: true,
    })
  },
  edit: function (item) {
    return hillo.postWithUploadFile('DCache.php?op=updateDCacheColor', item, {
      showLoading: true,
    })
  },
  remove: function (id) {
    return hillo.post('DCache.php?op=deleteDCacheColor', { id: id })
  },
  load: async function (filter) {
    return (await hillo.get('DCache.php', {
      op: 'showDCacheColor',
      ...filter,
    }))
      .content
  },

}

export default IKDataEntity.ModelFactory(entity, config)
